.header {
    background-color: #2c3e50;
    color: #ecf0f1;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header h1 {
    margin: 0;
    font-size: 1.5rem;
}

.header nav ul {
    display: flex;
    gap: 1rem;
}

.header nav ul li a {
    color: #ecf0f1;
    text-decoration: none;
    transition: color 0.3s ease;
}

.header nav ul li a:hover {
    color: #3498db;
}

/* Estilos para a imagem do perfil */
.profile-picture {
    width: 50px; /* Ajuste para o tamanho desejado */
    height: 50px; /* Mantém a imagem quadrada */
    border-radius: 50%; /* Torna a imagem redonda */
    object-fit: cover; /* Para garantir que a imagem se encaixe bem no espaço */
    margin-right: 1rem; /* Espaço entre a imagem e o título */
}

@media (max-width: 768px) {
    .header {
        flex-direction: column;
        text-align: center;
    }

    .header nav ul {
        margin-top: 1rem;
    }

    .profile-picture {
        width: 40px; /* Tamanho menor em telas menores */
        height: 40px; /* Mantém a proporção */
    }
}
